.container-loader {
  height: 100vh;
  width: 100vw;
}

.container-avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 3rem;
  background-color: white;
  height: 100vh;
  width: 100vw;
  padding: 1rem;
  box-sizing: border-box;
}

.loader-div {
  width: inherit;
  height: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader {
  max-inline-size: 100%;
}

.title-container h1 {
  color: #a7bc5b;
  font-size: 2rem;
  text-align: center;
}

.avatars {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  justify-content: center;
}

.avatar {
  border: 0.4rem solid transparent;
  padding: 0.4rem;
  border-radius: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s ease-in-out;
}

.avatar img {
  height: 20rem;
  transition: 0.5s ease-in-out;
}

.selected {
  border: 0.4rem solid #4e0eff;
}

.submit-btn {
  background-color: #8da242;
  color: white;
  padding: 1rem 2rem;
  border: none;
  font-weight: bold;
  cursor: pointer;
  border-radius: 0.4rem;
  font-size: 1rem;
  text-transform: uppercase;
}

.submit-btn:hover {
  background-color: #8da242;
}

#avatar-img {
  height: 8rem;
  width: 8rem;
}

@media screen and (max-width: 1024px) {
  .avatars {
    gap: 1.5rem;
  }

  .avatar img {
    height: 16rem;
  }

  .submit-btn {
    padding: 0.8rem 1.5rem;
    font-size: 0.9rem;
  }

  .title-container h1 {
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 768px) {
  .avatars {
    gap: 1rem;
  }

  .avatar img {
    height: 14rem;
  }

  .submit-btn {
    padding: 0.7rem 1.2rem;
    font-size: 0.85rem;
  }

  .title-container h1 {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 480px) {
  .container-avatar {
    gap: 2rem;
  }

  .avatar img {
    height: 12rem;
  }

  .submit-btn {
    padding: 0.6rem 1rem;
    font-size: 0.8rem;
  }

  .title-container h1 {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 320px) {
  .container-avatar {
    gap: 1.5rem;
  }

  .avatars {
    gap: 0.5rem;
  }

  .avatar img {
    height: 10rem;
  }

  .submit-btn {
    padding: 0.5rem 0.8rem;
    font-size: 0.7rem;
  }

  .title-container h1 {
    font-size: 1.2rem;
  }

  #avatar-img {
    height: 6rem;
    width: 6rem;
  }
}